<template>
	<div>
		<p class="jisp">{{worksCount}}</p>	
	    <div class="div">
			<el-row>
				<el-col :span="24">
					<el-row>
						<el-col :span="24">
							<div class="upload_label">
								<div class="mian">
									<label></label>
									<div class="file" id="Tematichesr">
							            <input type="file" id="zuye" name="file" @change="previewFile"/>
							        </div>
								</div>
								<div class="mian">
									<label></label>
									<el-input
									  type="textarea"
									  :rows="2"
									  placeholder=""
									  v-model="title">
									</el-input>
								</div>
							</div>
							<div class="upload_label">
								<el-input placeholder="" style="width: 240px"  v-model="queryCriteria.type">
								    <el-select slot="prepend" placeholder="" style="width: 110px" v-model="queryCriteria.typeValue">
								        <el-option label="ID" value="ID"></el-option>
								        <el-option label="" value=""></el-option>
								        <el-option label="" value=""></el-option>
								    </el-select>
							    </el-input>
							    <div class="type">	
							    	<label></label>
							    	<el-select v-model="queryCriteria.typeName" slot="prepend" placeholder="" style="width: 100px">
								        <el-option label="" value=""></el-option>
								        <el-option label="" value=""></el-option>
								    </el-select>
								    <label></label>
							    	<el-select v-model="queryCriteria.typeName" slot="prepend" placeholder="" style="width: 100px">
								        <el-option label="" value=""></el-option>
								        <el-option label="" value=""></el-option>
								    </el-select>
							    </div>
							    <el-button class="button" type="primary" @click="QueryCriteria"></el-button>
								<el-button type="danger" @click="reset"></el-button>
							</div>
							<el-row>
					    		<el-col :span="18" class="table">
							    	<el-table
										border
										stripe
									    :data="audioMaterial"
									    style="width: 100%">
									    <el-table-column
									      prop="id"
									      label="ID"
									      width="100">
									    </el-table-column>
									    <el-table-column
									      prop="materialName"
									      label=""
									      width="100">
									    </el-table-column>
									    <el-table-column
									      prop="type"
									      label="(minik/)"
									      width="180">
									    </el-table-column>
									     <el-table-column
									      prop="releaseName"
									      label=""
									      width="110">
									    </el-table-column>
									     <el-table-column
									      prop="releaseDate"
									      label=""
									      width="180">
									    </el-table-column>
									     <el-table-column
									      prop="information"
									      label=""
									      width="100">
									    </el-table-column>
									     <el-table-column
									      prop="publiCworks"
									      label="(minik/)"
									      width="200">
									    </el-table-column>
									    <el-table-column label="" width="200%">
									        <template scope="scope">
										        <el-button
										            size="small"
										            @click="biangeng(scope.$index, scope.row)"></el-button>
										        <el-button
										            size="small"
										            @click="biangeng(scope.$index, scope.row)"></el-button>
									        </template>
									    </el-table-column>
									</el-table>
									<!--  -->
									<el-dialog
										v-if=""
										title=""
										:visible.sync="sort"
										size="tiny">
										<label></label>
										 <el-input placeholder="" style="width: 100px"  v-model="sorts"></el-input>
										 <label></label>
										<span slot="footer" class="dialog-footer">
										    <el-button @click="sort = false"> </el-button>
										    <el-button type="primary" @click="sortsMethod "> </el-button>
										</span>
									</el-dialog>
						    	</el-col>
							</el-row> 		
				    		<el-row>
						    	<el-col :span="18" class="table">
						    		<el-pagination
								        @size-change="handleSizeChange"
								        @current-change="handleCurrentChange"
								        :current-page="currentPage"
								        :page-size="10"
								        layout="prev, pager, next, jumper"
								        :total="100">
								    </el-pagination>
						    	</el-col>
				    		</el-row>
						</el-col>
					</el-row>
					<el-button size="small" @click="SubmitButton"></el-button>
					<el-button size="small" @click="cancelButton"></el-button>
				</el-col>
			</el-row>
		</div>
	</div>
</template>
<script>
	//MD5
	import Md5 from 'js-md5';
	import browserMD5File  from 'browser-md5-file'

	import {mapMutations,mapActions, mapState} from "vuex"
	import {SWITCHtwo} from "../../../../store/operationManagement/popList.js"
	export default{
		name:"selectedList",
    	//HTML
		beforeMount(){
			this.title = this.getPopList.title
			this.coverUrl = this.getPopList.coverUrl
			this.id = this.getPopList.id
		},
		mounted(){
			let preview = document.getElementById("Tematichesr")
			preview.style.backgroundImage= `url(${this.coverUrl})`;
		},
		data(){
			return{
				imageUrl: '',
				 //
				queryCriteria:{
					type:"",
					typeValue:"",
					typeName:"",
					sharedState:"",
					grade:"",
					platform:"",
					nameType:""
				},
				//
				currentPage:1,
				//
				dialogVisible: false,
				//
				sort:false,
				sorts:"",
				//
				sortsFront:"",
				//
				sortsAfter :"",
				//
				sortsEntry :{},
				//
				textarea:"",
				audioMaterial:[{
					id:"1",
					materialName:"1",
					type:"",
					releaseName:"1",
					releaseDate:"2017-8-1 15:36:26",
					sharedState:"",
					publiCworks :"",
					position:"",
					information:"",
					just:"",
					akes:""
				}],
				//
				title:"",
				coverUrl:"",
				id:"",

			}
			
		},
		computed:{
	    	...mapState("popList",["getPopList","url"]),
	    	worksCount:{
		      	get(){
		      		return this.$store.state.popList.getPopList.worksCount
		      	},
		      	set(){}
		    },
	    },
		methods:{
			...mapMutations("popList",["SWITCHtwo","uploadPicture","dataURItoBlob","setAddPop"]),
			...mapActions("popList",["GETTUPIAN","EDITPOP"]),
			handleSizeChange(val) {
        		console.log(` ${val} `);
        	},
        	handleCurrentChange(val) {
        		console.log(`: ${val}`);
        	},
        	SubmitButton(){
        		if (this.url !== "") this.coverUrl = this.url
        		
        		if (this.coverUrl === "") {
		    		this.$message({
			          showClose: true,
			          message: '',
			          type: 'warning'
			        });
			       	return
		    	}
		    	if (this.title === "") {
		    		this.$message({
			          showClose: true,
			          message: '',
			          type: 'warning'
			        });
			       	return
		    	}
		    	this.EDITPOP({
		    		coverUrl:this.coverUrl,
		    		title:this.title,
		    		id:this.id
		    	})
        	},
        	cancelButton(){
        		this.title = ""
        		this.setAddPop()
        		this.SWITCHtwo()
        	},
		    QueryCriteria(){
        		console.log(this.queryCriteria)
        	}, 
        	reset(){
        		let qreryCriterias={
					type:"",
					typeValue:"",
					typeName:"",
					sharedState:"",
				}
				this.queryCriteria = qreryCriterias
        	},
        	 biangeng(index,row){
		    	this.sortsFront = index
		    	this.sortsEntry = row
		    	this.sort = true
		    	// this.audioMaterial.splice(3,0,row)
		    	// console.log(index,row)
		    },
        	sortsMethod(){
		    	this.audioMaterial.splice(`parseInt(this.sorts)-1`,0,this.sortsEntry)
		    	this.sort = false
		    },
		    //
		    previewFile(event) {
        		var thist =this
        	    let UploadPicture={}
		       var file = event.target.files[0];
		      	var  readers = new FileReader()
		      	var reader = new FileReader();
				reader.onload = function(event) {
					preview.style.backgroundImage= `url(${reader.result})`;
				};
				readers.onload = function(event) {
					thist.dataURItoBlob(readers.result)
				};
				if (file) {
		       	reader.readAsDataURL(file);
		      		readers.readAsArrayBuffer(file)
		       }
		      	var preview = document.getElementById("Tematichesr")
		      	browserMD5File(file, function (err, md5) {
				    UploadPicture.md5s = md5
				    UploadPicture.fsize = file.size
		      		UploadPicture.fileext = file.type
		      		UploadPicture.sign = Md5(`${UploadPicture.md5s}key`)
		      		thist.uploadPicture(UploadPicture)
		      		thist.GETTUPIAN()
				});
			      	
			},
		}
	}
</script>
<style scoped>
	.file,.centent{
	    background-color: #fbfdff;
	    border: 1px dashed #c0ccda;
	    border-radius: 6px;
	    box-sizing: border-box;
	    width: 300px;
	    height: 150px;
	    cursor: pointer;
	    line-height: 146px;
	    vertical-align: top;
	    background-size:100% 100% ;
    }
    .file input{
	    position: relative;
	    top: -70px;
	    height: 100%;width: 100%;
	    opacity: 0;
	    cursor: pointer;
	}
	.center{
		margin-top: 20px;
		text-align: center;
	}
	.avatar-uploader .el-upload {
	    border: 1px dashed #d9d9d9;
	    border-radius: 6px;
	    cursor: pointer;
	    position: relative;
	    overflow: hidden;
	}
	.avatar-uploader .el-upload:hover {
	    border-color: #20a0ff;
	}
	.avatar-uploader-icon {
	    font-size: 28px;
	    color: #8c939d;
	    width: 178px;
	    height: 178px;
	    line-height: 178px;
	    text-align: center;
	}
	.avatar {
	    width: 178px;
	    height: 178px;
	    display: block;
	}
	.upload_label{
	  	display: flex;
	  	margin-bottom: 20px;
	}
	.upload_label >*{
	  	margin-right: 20px;
	}
	.upload_label >label{
	  	margin-right: 20px;
	}
	.table{
		padding-top: 20px;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-end;
	}
	.mian{
		display: flex;
		margin-right: 30px;
		width: 450px;
	}
	.mian > label{
		white-space: nowrap;
		margin-right: 20px;
	}
	.jisp{
		margin: 0;
		height: 70px;
		color: #353535;
		font-size: 20px;
		line-height: 70px;
		background-color: #f2f2f2;
		padding-left: 30px;
		margin-bottom: 25px;
	}
	.div{
		padding:0 30px;
	}
</style>


